import { useCallback, useEffect, useState } from "react";
import { getAllOrders, getOrderById, getStatusHistory } from "../api/orderApis";

const useOrder = (orderId) => {
  const [orders, setOrders] = useState([]);
  const [order, setOrder] = useState(null);
  const [statusHistory, setStatusHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchOrders = useCallback(async () => {
    if (orderId) return;
    setLoading(true);
    setError(null);
    try {
      const response = await getAllOrders();
      if (response?.status) {
        console.log(response.data);
        setOrders(response.data);
      }
    } catch (error) {
      console.error("Error fetching orders", error);
      setError("Failed to fetch orders. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [orderId]);

  const fetchOrder = useCallback(async () => {
    if (!orderId) return;
    setLoading(true);
    setError(null);
    try {
      const response = await getOrderById(orderId);
      if (response?.status) {
        console.log(response.data);
        setOrder(response.data);
      }
    } catch (error) {
      console.error("Error fetching order details", error);
      setError("Failed to fetch order details. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [orderId]);

  const fetchStatusHistory = useCallback(async () => {
    if (!orderId) return;
    setLoading(true);
    setError(null);
    try {
      const response = await getStatusHistory(orderId);
      if (response.status) {
        console.log(response.data);
        setStatusHistory(response.data.statusDetails.orderStatusDetails);
      }
    } catch (error) {
      console.error("Error fetching status history", error);
      setError("Failed to fetch status history. Please try again.");
    } finally {
      setLoading(false);
    }
  }, [orderId]);

  useEffect(() => {
    fetchOrders();
  }, [fetchOrders]);

  useEffect(() => {
    fetchOrder();
  }, [fetchOrder]);

  useEffect(() => {
    fetchStatusHistory();
  }, [fetchStatusHistory]);

  return { orders, order, statusHistory, loading, error };
};

export default useOrder;
