import { getCookie } from "../utils/cookie";
import axiosInstance from "./axiosInstance";

const getAllOrders = () => {
  const token = getCookie("accessToken");
  return axiosInstance.get("/getOrderDetails", {
    headers: {
      authorization: `Bearer ${token}`,
    },
  });
};

const getOrderById = (orderId) => {
  const token = getCookie("accessToken");
  return axiosInstance.get(`/getOrderById/${orderId}`, {
    headers: {
      authorization: `Bearer ${token}`,
    },
  });
};

const createOrder = (orderItems, address, pricingDetails, paymentMethod) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(
    "/placeOrder",
    {
      orderItem: orderItems,
      address,
      ...pricingDetails,
      payment_method: paymentMethod,
    },
    {
      headers: {
        authorization: `Bearer ${token}`,
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const editOrder = (
  orderId,
  address,
  pricingDetails,
  paymentMethod,
  hasPaymentCompleted,
  status
) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(
    `/editOrder/${orderId}`,
    {
      address,
      ...pricingDetails,
      payment_method: paymentMethod,
      payment_complete: hasPaymentCompleted,
      status: status,
    },
    {
      headers: {
        authorization: `Bearer ${token}`,
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const updateOrderStatus = (orderId, status) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(
    `/updateStatus/${orderId}`,
    { status },
    {
      headers: {
        authorization: `Bearer ${token}`,
        "x-api-key": process.env.REACT_APP_ORDER_API_KEY,
      },
    }
  );
};

const getStatusHistory = (orderId) => {
  return axiosInstance.get(`/orderStatusDetails/${orderId}`);
};

export {
  getAllOrders,
  getOrderById,
  createOrder,
  editOrder,
  updateOrderStatus,
  getStatusHistory
};
