import { FiDownload } from "react-icons/fi";
import PurchaseTable from "./PurchaseTable";
import OrderStatus from "./OrderStatus";
import { useParams } from "react-router-dom";
import useOrder from "../../hooks/useOrder";
import { formatDate } from "../../utils/dateUtils";
import { getColorForStatus } from "../../utils/Constants";
import Button from "../../components/Button";

const Order = () => {
  const { orderId } = useParams();
  const { order, loading, error, statusHistory } = useOrder(orderId);

  if (loading) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          Loading...
        </h2>
      </div>
    );
  }

  if (error) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          {error.message}
        </h2>
      </div>
    );
  }

  return (
    order && (
      <main className="container max-w-full py-8 px-32 max-xl:px-16 max-lg:px-8 max-md:px-4 space-y-4">
        <section className="TOP-HEAD">
          <div className="flex items-center gap-4">
            <h2 className="font-secondary font-semibold text-3xl">
              Order Details
            </h2>
            <span
              className={`px-3 py-1 rounded-full ${getColorForStatus(
                order.status
              )}`}
            >
              <p className="text-xs text-white font-medium">
                {order.status.toUpperCase()}
              </p>
            </span>
          </div>

          <p className="font-medium text-secondary font-secondary">
            Order placed on {formatDate(new Date(order.createdAt))}
          </p>

          {/* <p className="font-medium text-secondary font-secondary">
          Arriving on {formatDate(new Date(order.createdAt))}
          </p> */}
        </section>

        <Button
          type="button"
          reverse
          className="px-4 py-2 rounded-full ml-auto"
        >
          <div className="flex items-center gap-2">
            <FiDownload size={18} />
            <p className="font-medium">INVOICE</p>
          </div>
        </Button>

        <PurchaseTable order={order} />
        <OrderStatus
          statusHistory={statusHistory}
          currentStatus={order.status}
        />
      </main>
    )
  );
};

export default Order;
